@import url('https://fonts.googleapis.com/css2?family=Space+Mono&display=swap');

* {margin: 0; padding: 0;}
ul {list-style: none;}
a {all: unset; cursor: pointer;}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 10%;
    img {
        width: 216px;
    }
    li {
        padding-left: 45px;
    }
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 7px 0;
    border-top: 1px solid black;
    &__nav {
        width: 43%;
        margin: auto;
    }
    li:last-child:hover {
        pointer-events: none;
    }
}

.menu {
    display: flex;
    justify-content: space-between;
    font-family: 'Space Mono', monospace;
    line-height: 24px;
    text-transform: uppercase;
    li {
        &:hover {
            font-weight: 700;
        }
    } 
}